        //     $(this).append("<div><iframe height='"+iframeheight+"' id='zl-xxlst29' style='width:100%;display:block' scrolling='no' border='0' frameborder='no' src='//tu.baixing.com/widget/w/zl-xxlst29.html?autosize=1'></iframe><iframe height='"+iframeheight+"' id='zl-xxlst39' style='width:100%;display:block' scrolling='no' border='0' frameborder='no' src='//tu.baixing.com/widget/w/zl-xxlst39.html?autosize=1'></iframe></div>")
        //   }
        //   else if(item == that.datalistAd.length - 1) {
        //     $(this).append("<div><iframe height='"+iframeheight+"' id='zl-xxlst30' style='width:100%;display:block' scrolling='no' border='0' frameborder='no' src='//tu.baixing.com/widget/w/zl-xxlst30.html?autosize=1'></iframe><iframe height='"+iframeheight+"' id='zl-xxlst40' style='width:100%;display:block' scrolling='no' border='0' frameborder='no' src='//tu.baixing.com/widget/w/zl-xxlst40.html?autosize=1'></iframe></div>")
        //   }

          
        // })
        // console.log("第一组的广告加载完")
        //自动执行上拉加载的逻辑
        if(that.once) {
          that.once = false //防止重新执行定时器
          that.automation()
        }
      })
    },
    automation() {
      console.log(this.once)
      console.log(421)
      console.log(this.OnceTimes)
  　　this.timer = setInterval(() => {
        this.loadingtimes ++;
        console.log("上拉加载启动")
        this.upCallback()
        if(this.loadingtimes >=5) {
          clearInterval(this.timer)
        }
  　　}, this.OnceTimes)
    },
    randomNum() {//7个随机数的生成
      var randomNum = "";
      for(var i = 0;i<7;i++) {
        var num = Math.floor(Math.random()*10)
        randomNum = randomNum + num
      }
      return randomNum
    }
  },
  watch: {
    // 监听路由变化-用于非首次加载
    $route: function(newdata, olddata) {
      let localroute = localStorage.getItem("route");
      if (localroute == this.$route.params.id) {
        this.loadingDialog("hide");
        this.componentshow = true;
      } else {
        this.type = this.$route.params.id;
        // console.log(this.type)
        this.loadingDialog("show");
        this.componentshow = false;
        this.getDetailList(this.type);
        // this.mescroll.scrollTo(0, 0)
        console.log("监听路由的变化1");
        console.log(this.$route.params);

        //清除定时器
        clearInterval(this.timer)
        this.once = true
        this.loadingtimes = 0
        this.loadingtimes = 0//重置自动加载的次数
      }
      // if(localStorage.getItem('route'))
    }
  }
};
</script>

<style lang="scss" scoped="">
@import "@/assets/styles/common.scss";
//三图的样式

.news-list-wrap {
  background-color: #fff;
  position: relative;

  .news-item,
  .news-item-s2,.news-ad {
    height: auto;
    position: relative;
    &:before {
      content: "";
      position: absolute;
      width: 187.5%;
      height: 1px;
      bottom: 0;
      left: 3%;
      right: 0;
      top: 0;
      margin:0 auto;
      // transform: translateX(-50%);
      border-bottom: 1px solid #e6e6e6;
      transform-origin: 0 0;
      transform: scale(0.5, 0.5);
      box-sizing: border-box;
    }
    a {
      display: block;
      padding: 10px 0;
      // border-bottom: 1px solid #f5f5f5;
      color: #333;

      .news-wrap {
        position: relative;
        min-height: 115px;
        h3 {
          font-size: 15px;
          line-height: 1.2em;
          overflow: hidden;
          width: 100%;
          max-height: 40px;
          margin-bottom: 15px;
          font-weight:800;
          color: #222222;
        }
        .img-wrap {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          display: -webkit-flex;
          overflow: hidden;
          height: px2rem(75px);
          .img {
            width: 33.33%;
            padding-right: 2px;
            div {
              width: 100%;

              background-color: rgba(150, 150, 150, 0.1);
              // background-image: url(//mini.eastday.com/toutiaoh5/img/img_preview.png);
              background-repeat: no-repeat;
              background-position: center;
              background-size: cover;
              height: 100%;
              border-radius: 2px;
            }
          }
        }
        .tags {
          margin-top: 15px;
          color: #999999;
          overflow: hidden;
          font-size: 12px;
          font-family:PingFang-SC-Medium;
          .tag {
            display: inline-block;
            line-height: 12px;
            margin-right: 5px;
          }
          .tag-view {
            float: right;
            margin-right: 0px;
          }
        }
      }
    }
  }
  .news-item-s1 {
    a {
      .news-wrap {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        display: -webkit-flex;
        min-height: 0;
        .txt-wrap {
          position: relative;
          width: 67%;
          h3 {
            font-size: 15px;
            line-height: 1.2em;
            overflow: hidden;
            margin-right: 12px;
            height: 38px;
            color: #222222;
            font-weight:800;
            text-overflow: ellipsis;
            word-wrap: break-word;
            word-break: break-all;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;

            .tags {
              position: absolute;
              bottom: 0;
              color: #999999;
              overflow: hidden;
              font-size: 12px;
              font-family:PingFang-SC-Medium;
            }
          }
          p {
            width: 100%;
            position: absolute;
            bottom: 0;
            .tag-view {
              float: right;
              font-family:PingFang-SC-Medium;
            }
          }
        }
        .img-wrap {
          overflow: hidden;
          width: 33%;
          height: px2rem(75px);
          margin-right: 10px;
          div {
            width: 100%;
            background-color: rgba(150, 150, 150, 0.1);
            // background-image: url(//mini.eastday.com/toutiaoh5/img/img_preview.png);
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            height: 100%;
            border-radius: 2px;
          }
        }
      }
    }
  }
}
.mescroll {
  position: fixed;
  top: 35px;
  bottom: 0;
  height: auto; /*如设置bottom:50px,则需height:auto才能生效*/
  max-width: 750px;
}
.news-ad {
  position: relative;
}
.news-ad:before {
    content: "";
    position: absolute;
    width: 200%;
    height: 1px;
    bottom: 0;
    left: 0;
    border-bottom: 1px solid #e6e6e6;
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    -webkit-transform: scale(0.5, 0.5);
    transform: scale(0.5, 0.5);
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
.adfour2 {
  padding: 0 5px;
}
</style>



